@import "_sass/common";
@import "_sass/base";



/****** COMMON *******/

.cf {
	display: block;
	clear: both;
	content: "";
}



/******* HEADER *******/

#header {
	position: relative;
	width: 100%;
	background: $color-white;
	box-shadow: 0 0 1px $color-grey;
}

#headerMenu {
	position: relative;
	margin: 0 auto 0 auto;
	width: 100%;
	height: 75px;
	max-width: 1000px;
}

#headerMenu #headerLogo {
	z-index: 10;
	position: absolute;
	left: -20px;
	top: -15px;
	width: 105px;
	height: 120px;
}

#headerMenu #headerLogo a {
	display: block;
	width: 100%;
	height: 100%;
	background: url("../../img/global/globalLogo.svg") center center no-repeat transparent;
	background-size: contain;
}

#headerMenu ul,
#headerMenu ul li {
	list-style: none;
}

#header #headerMenu ul.menu {
	display: inline-block;
	padding-left: 100px;
	height: 100%;
	background: $color-white;
}

#header #headerMenu ul.menu li {
	display: table;
	float: left;
	height: 75px;
}

#header #headerMenu ul.menu li a {
	display: table-cell;
	padding: 0 15px 0 15px;
	width: 100%;
	height: 75px;
	color: $color-steamchat;
	font-weight: 400;
	font-size: 18px;
	line-height: 18px;
	text-align: center;
	text-decoration: none;
	text-transform: uppercase;
	vertical-align: middle;
}

#header #headerMenu ul.menu li a:hover {
	color: $color-white;
	background: $color-steamchat;
	cursor: pointer;
}

#header #headerMenu ul.social {
	float: right;
	height: 100%;
}

#header #headerMenu ul.social:after {
	@include clearFix;
}

#header #headerMenu ul.social li {
	float: left;
	display: table;
	width: 40px;
	height: 75px;
}

#header #headerMenu ul.social li > a {
	display: table-cell;
	width: 100%;
	height: 100%;
	vertical-align: middle;
}

#header #headerMenu ul.social li > a:hover {
	background: $color-steamchat;
}

#header #headerMenu ul.social li > a > .icon-element {
	margin: 0 auto 0 auto;
	display: block;
	width: 24px;
	height: 24px;
}

#header #headerMenu ul.social li > a > .icon-element > .layer {
	fill: $color-steamchatLight;
}

#header #headerMenu ul.social li > a:hover > .icon-element > .layer {
	fill: #FFFFFF;
}



/******* CONTENT *******/

#content {
	margin: 0 auto 0 auto;
	padding: 100px 20px 250px 20px;
	width: 100%;
	max-width: 700px;
}

#content h1 {
	margin-bottom: 20px;
	font-size: 38px;
	line-height: 38px;
	font-weight: 300;
	color: $color-steamchat;
}

#content h2 {
	margin-top: 40px;
	margin-bottom: 20px;
	font-size: 24px;
	line-height: 24px;
	font-weight: 300;
}

#content p {
	margin-bottom: 20px;
	text-align: justify;
}

#content > p:last-child {
	margin-bottom: 0;
}

#content p a,
#content ul a {
	color: $color-steamchat;
}

#content ul a:hover,
#content p a:hover {
	color: $color-steamchatDark;
}

#content ul,
#content li {
	list-style: none;
}

#content #aboutLinks li {
	display: table;
	float: left;
	width: 50%;
	height: 100px;
}

#content #aboutLinks li .wrapper {
	display: table-cell;
	width: 100%;
	height: 100%;
	text-align: center;
	vertical-align: middle;
}

#content #aboutLinks li a {
	font-weight: 700;
	line-height: 18px;
	text-decoration: none;
}

#content #aboutLinks li a .subtitle {
	display: block;
	padding-top: 10px;
	font-weight: 400;
	font-size: 16px;
	line-height: 16px;
	text-decoration: underline;
}

#content #specialsColumns {
	width: 100%;
}

#content #specialsColumns > div {
	width: 50%;
}

#content #specialsColumns .left {
	float: left;
}

#content #specialsColumns .right {
	float: right;
}

#content #specialsColumns ul {
	padding-left: 15px;
}

#content #specialsColumns div > ul {
	border-left: 5px solid $color-steamchat;
}



/******* FOOTER *******/

#footer {
	width: 100%;
	background: #EEEEEE;
}

#footer .wrapper {
	margin: 0 auto 0 auto;
	padding: 50px 20px 75px 20px;
	max-width: 900px;
	overflow: auto;
}

#footer-logo {
	opacity: .5;
	width: 80px;
	height: 80px;
	float: left;
	margin-right: 50px;
	background: url("../../img/global/globalLogo.svg") center center no-repeat transparent;
	background-size: contain;
	@include filterGrayscale (100%);
}

#footer-logo:hover {
	opacity: .8;
}

#footer .left,
#footer .right {
	width: 50%;
}

#footer .left {
	float: left;
}

#footer .right {
	float: right;
}

#footer ul,
#footer li {
	list-style: none;
}

#footer .left ul {
	float: left;
	width: 150px;
}

#footer li {
	margin-top: 20px;
	padding: 0;
	font-size: 12px;
	line-height: 12px;
	color: $color-greyLight;
}

#footer .left li {
	float: left;
	width: 75px;
}

#footer .right li {
	text-align: right;
}

#footer li.title {
	width: 150px;
	font-size: 14px;
	line-height: 14px;
	font-weight: bold;
	color: $color-greyLight;
}

#footer li:first-child {
	margin: 0;
}

#footer li a {
	color: inherit;
}

#footer li a:hover {
	color: $color-grey;
}



/********* RESPONSIVE *********/

@media (max-width: 1000px) {

	#headerMenu,
	#playerWrapper {
		left: 0 !important;
		margin: 0 !important;
		width: 100% !important;
	}

	#headerMenu {
		background: #FFFFFF !important;
	}

}

@media (max-width: 800px) {

	body {
		font-size: 12px;
	}

	#footer .left ul {
		display: none;
	}

	#header {
		border-bottom: 1px solid #CCCCCC;
		box-shadow: none;
	}

	#headerMenu #headerLogo {
		left: -7px;
		top: -7px;
		width: 65px;
		height: 70px;
	}

	#headerMenu,
	#header #headerMenu ul.menu li,
	#header #headerMenu ul.menu li a {
		height: 50px;
	}

	#header #headerMenu {
		position: static;
		margin: 0;
		background: #FFFFFF !important;
	}

	#header #headerMenu ul.menu {
		float: right;
		padding-left: 0;
		padding-right: 10px;
	}

	#header #headerMenu ul.menu li a {
		padding: 0 10px 0 10px;
		font-size: 10px;
		line-height: 10px;
	}

	#header #headerMenu ul.social {
		display: none;
	}

	#content {
		padding: 40px 20px 40px 20px !important;
	}

	#content h1,
	#content h2 {
		margin-bottom: 10px;
	}

	#content h1 {
		font-size: 20px;
		line-height: 20px;
	}

	#content h2 {
		margin-top: 20px;
		font-size: 16px;
		line-height: 16px;
	}

	#content p {
		margin-bottom: 10px;
		font-size: 12px;
		line-height: 20px;
	}

	#content #aboutLinks li,
	#content #aboutLinks li .wrapper,
	#content #aboutLinks li a {
		display: block;
		height: auto;
	}

	#content #aboutLinks li {
		float: none;
		width: 100%;
	}

	#content #aboutLinks li a {
		padding: 10px 0 10px 0;
	}

	#content #aboutLinks li a .subtitle {
		padding-top: 0;
		font-size: 12px;
		line-height: 12px;
	}

	#content #specialsColumns > div {
		float: none;
		width: 100%;
	}

	#footer * {
		float: none !important;
	}

	#footer .wrapper {
		padding: 20px;
	}

 	#footer .left,
 	#footer .right,
 	#footer ul {
 		width: 100% !important;
 	}

	#footer-logo {
		display: block;
		margin: 0 auto 20px auto;
		width: 50px;
		height: 50px;
	}

	#footer .right li {
		margin-top: 10px;
		font-size: 10px;
		line-height: 10px;
		text-align: center;
	}

	#footer .right li:first-child {
		margin-top: 0;
	}

}